import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import withErrorApi from '../../hoc/withErrorApi/withErrorApi';
import { getApiMarvel } from '../../utils/getApiMarvel';
import { sortByNameOrder, sortByNameOutOfOrder } from '../../utils/sort';
import { API_CHARACTERS, QUERY_NAME_START_WITH, QUERY_OFFSET } from '../../Constants/Api';
import CharactersList from '../../Components/CharactersList/CharactersList';
import Loader from '../../Components/Loader/Loader';
import Button from '../../Components/UI/Button/Button';
import Input from '../../Components/UI/Input/Input';
import classes from './Characters.module.css';

const SEARCH_QUERY = `${API_CHARACTERS}?${QUERY_NAME_START_WITH}`;
const OFFSET_QUERY = `${API_CHARACTERS}?${QUERY_OFFSET}`;

const Characters = ({ setErrorApi }) => {
  const [character, setCharacter] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isInputSubmitted, setIsInputSubmitted] = useState(false);
  const [searchName, setSearchName] = useState('');
  const [offsetSearch, setOffsetSearch] = useState(0);
  const [sortBy, setSortBy] = useState(true);
  const [forwardButtonBlock, setForwardButtonBlock] = useState(false);
  const cleanupFunction = useRef(false);

  let inputValue = '';

  const getCharacters = async url => {
    const res = await getApiMarvel(url);
    if (res) {
      const characterList = res.map(({ id, name, thumbnail: { path, extension } }) => ({
        id, name, path, extension,
      }));
      if (!cleanupFunction.current) {
        setCharacter(characterList);
        setIsLoading(false);
        setErrorApi(false);
      }
    } else {
      setErrorApi(true);
    }
  };

  useEffect(() => {
    cleanupFunction.current = false;
    if (searchName) {
      const search = `${SEARCH_QUERY}=${searchName}`;
      getCharacters(search);
      setOffsetSearch(0);
      setForwardButtonBlock(true);
    } else {
      const offset = `${OFFSET_QUERY}=${offsetSearch}`;
      getCharacters(offset);
      setForwardButtonBlock(false);
    }

    // console.log('Characters rendered');

    return () => {
      setSortBy(true);
      cleanupFunction.current = true;
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchName, offsetSearch]);

  const sortCharactersByOrder = () => {
    const sortedCharacters = sortByNameOrder(character);
    setCharacter(sortedCharacters);
    setSortBy(!sortBy);
  };

  const sortCharactersOutOfOrder = () => {
    const sortedCharacters = sortByNameOutOfOrder(character);
    setCharacter(sortedCharacters);
    setSortBy(!sortBy);
  };

  const offsetBack = () => {
    if (offsetSearch !== 0) {
      setOffsetSearch(offsetSearch - 30);
    }
    setSortBy(true);
    setIsLoading(true);
  };

  const offsetForward = () => {
    if (offsetSearch < 1493) {
      setOffsetSearch(offsetSearch + 30);
    }
    setSortBy(true);
    setIsLoading(true);
  };

  const getInputValue = name => {
    inputValue = name;
    setIsInputSubmitted(false);
  };

  const submitQuery = name => {
    setSearchName(name);
    inputValue = '';
    setIsInputSubmitted(true);
  };

  const inputEnterHandler = () => {
    if (inputValue) {
      submitQuery(inputValue);
    }
  };

  return (
    <section className={classes.characters__wrapper}>
      <Input searchName={name => getInputValue(name)} onkeydown={() => inputEnterHandler()} isSubmitted={isInputSubmitted} />
      <Button nameBtn="search by name" nameAtr="SearchByName" onClick={() => submitQuery(inputValue)} disabled={isLoading} />
      <hr />
      <Button nameBtn="sort by" nameAtr="sortBy" onClick={() => sortCharactersByOrder()} disabled={isLoading || sortBy} />
      <Button nameBtn="sort out" nameAtr="sortOut" onClick={() => sortCharactersOutOfOrder()} disabled={isLoading || !sortBy} />
      <Button nameBtn="prev" nameAtr="prevCharacter" onClick={() => offsetBack()} disabled={isLoading || !offsetSearch} />
      <Button nameBtn="next" nameAtr="nextCharacter" onClick={() => offsetForward()} disabled={isLoading || forwardButtonBlock} />

      { isLoading
        ? <Loader />
        : <CharactersList character={character} />}
    </section>
  );
};

Characters.propTypes = {
  setErrorApi: PropTypes.func,
};

export default withErrorApi(Characters);
