import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { PORTRAIT_UNCANNY } from '../../../Constants/Api';
import { setComicsToFavorite, removeComicsFromFavorite } from '../../../store/actions/favoriteActions';
import iconFavoriteFill from './img/favoriteFill.svg';
import iconFavoriteEmpty from './img/favoriteEmpty.svg';
import classes from './ComicsItem.module.css';

const ComicsItem = ({ comics, setImgWidth }) => {
  const imgWidth = useRef(0);
  const comicsState = useSelector(state => state.comicsReducer);
  const dispatch = useDispatch();

  const dispatchComicsFavorite = (...comicsItem) => {
    const [id, title, imgSrc] = comicsItem;

    if (comicsState[id]) {
      dispatch(removeComicsFromFavorite([id]));
    } else {
      const item = {
        [id]: {
          title,
          img: imgSrc,
        },
      };
      dispatch(setComicsToFavorite(item));
    }
  };

  useEffect(() => {
    const imageWidth = Number.parseInt(getComputedStyle(imgWidth.current).width, 10);
    setImgWidth(imageWidth + 100);

    return () => {
      imgWidth.current = 0;
    };
  }, []);

  return (
    <>
      {
                comics.map(({
                  id, title, path, extension,
                }, idx) => {
                  if (path.lastIndexOf('image_not_available') === -1) {
                    const imgSrc = `${path}/${PORTRAIT_UNCANNY}.${extension}`;
                    return (
                      <div key={title + idx} className={classes.comics__item}>
                        <h3 className={classes.comics__name}>{title}</h3>
                        <img src={imgSrc} alt="img_comics" className={classes.comics__img} ref={imgWidth} />
                        <img
                          src={comicsState[id] ? iconFavoriteFill : iconFavoriteEmpty}
                          onClick={() => dispatchComicsFavorite(id, title, imgSrc)}
                          alt="icon-favorite"
                          className={classes.comics__favorite}
                        />
                      </div>
                    );
                  }
                  return null;
                })
            }
    </>
  );
};

ComicsItem.propTypes = {
  comics: PropTypes.array,
  setImgWidth: PropTypes.func,
};

export default ComicsItem;
