import React, { useRef, useState } from 'react';
import PropTypes from 'prop-types';
import classes from './FavoriteItem.module.css';
import iconFavoriteFill from './img/favoriteFill.svg';
import iconFavoriteEmpty from './img/favoriteEmpty.svg';

const FavoriteItem = ({ items, removeItem }) => {
  const [arrayOfId, setArrayOfId] = useState([]);
  const refId = useRef([]);

  const isFavoriteHandler = id => {
    if (arrayOfId.includes(id)) {
      const newArray = [...arrayOfId].filter(item => item !== id);
      setArrayOfId(newArray);
      refId.current = [...refId.current].filter(item => item !== id);
      removeItem(refId.current);
    } else {
      setArrayOfId([...arrayOfId, id]);
      refId.current = [...refId.current, id];
      removeItem(refId.current);
    }
  };

  return (
    <ul className={classes.character__list}>
      {

                     items.map(({ id, title, img }, idx) => (
                       <li key={title + idx} className={classes.character__item}>
                         <h3 className={classes.character__name}>{title}</h3>
                         <img
                           src={img}
                           alt="favorite-img"
                           className={classes.character__img}
                           style={arrayOfId.includes(id) ? { opacity: 0.3 } : { opacity: 1 }}
                         />
                         <img
                           src={arrayOfId.includes(id) ? iconFavoriteEmpty : iconFavoriteFill}
                           alt="icon-favorite"
                           className={classes.character__favorite}
                           onClick={() => {
                             isFavoriteHandler(id);
                           }}
                         />
                       </li>
                     ))

            }
    </ul>
  );
};

FavoriteItem.propTypes = {
  items: PropTypes.array,
  removeItem: PropTypes.func,
};

export default FavoriteItem;
