import axios from 'axios';
import md5 from 'md5';
import {
  PUBLIC_KEY, PRIVATE_KEY, BASE_URL,
} from '../Constants/Api';

const TS = Date.now();
const HASH = md5(TS + PRIVATE_KEY + PUBLIC_KEY);

export default axios.create({
  baseURL: BASE_URL,
  params: {
    ts: TS,
    apikey: PUBLIC_KEY,
    hash: HASH,
    limit: 30,
  },
});
