import React from 'react';
import PropTypes from 'prop-types';
import classes from './Select.module.css';

const Select = ({
  formHandler, comicsYear, setComicsYear, yearList,
}) => (
  <form onSubmit={e => formHandler(e)} className={classes.form__wrapper}>
    <span className={classes.select__title}>choose year</span>
    <select
      value={comicsYear}
      onChange={e => setComicsYear(e.target.value)}
      className={classes.select}
    >
      {yearList.map((year, idx) => (
        <option value={year} key={idx + 1}>{year}</option>
      ))}
    </select>
    <input type="submit" value="search" className={classes.select__button} />
  </form>
);

Select.propTypes = {
  formHandler: PropTypes.func,
  comicsYear: PropTypes.string,
  setComicsYear: PropTypes.func,
  yearList: PropTypes.array,
};

export default Select;
