import React from 'react';
import { NavLink } from 'react-router-dom';
import { useSelector } from 'react-redux';
import classes from './Header.module.css';
import iconFavoriteFill from './img/favoriteFill.svg';

const Header = () => {
  const comicsReducer = useSelector(state => state.comicsReducer);
  const characterReducer = useSelector(state => state.characterReducer);

  const links = [
    { to: '/', label: 'Marvel App', exact: true },
    { to: '/characters', label: 'Characters', exact: false },
    { to: '/comics', label: 'Comics', exact: false },
    { to: '/favorites', label: 'Favorites', exact: false },
  ];

  const checkFavoriteIcon = Object.values(comicsReducer).length || Object.values(characterReducer).length;

  return (
    <header className={classes.header}>
      <nav>
        <ul className={classes.list}>
          {
                    links.map((link, idx) => (
                      <li key={idx} className={classes.list__item}>
                        <NavLink
                          to={link.to}
                          exact={link.exact}
                          activeClassName={link.exact ? classes.active : classes.active__links}
                        >
                          {link.label}
                          {checkFavoriteIcon && link.label === 'Favorites'
                            ? <img src={iconFavoriteFill} alt="icon-favorite" className={classes.character__favorite} />
                            : null}
                        </NavLink>
                      </li>
                    ))
                }
        </ul>
      </nav>
    </header>
  );
};

export default Header;
