import { ADD_COMICS_TO_FAVORITE, FETCH_COMICS_START, REMOVE_COMICS_FROM_FAVORITE } from '../constants/actionTypes';
import { removeItemFromStore } from '../../utils/removeItemFromStore';
import { getLocalStorage } from '../../utils/localStorage';

const initialState = getLocalStorage('comics');
// const initialState = {};

const comicsReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_COMICS_START:
      return {
        ...state,
        ...action.payload,
      };
    case ADD_COMICS_TO_FAVORITE:
      return {
        ...state,
        ...action.payload,
      };
    case REMOVE_COMICS_FROM_FAVORITE:
      // console.log('comicsReducer: ', action.payload);
      return removeItemFromStore(state, action.payload);
    default:
      return state;
  }
};

export default comicsReducer;
