import { ADD_CHARACTER_TO_FAVORITE, REMOVE_CHARACTER_FROM_FAVORITE, FETCH_CHARACTERS_START } from '../constants/actionTypes';
import { removeItemFromStore } from '../../utils/removeItemFromStore';
import { getLocalStorage } from '../../utils/localStorage';

const initialState = getLocalStorage('characters');

// const initialState = {};

const characterReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_CHARACTERS_START:
      return {
        ...state,
        ...action.payload,
      };
    case ADD_CHARACTER_TO_FAVORITE:
      return {
        ...state,
        ...action.payload,
      };
    case REMOVE_CHARACTER_FROM_FAVORITE:
      // console.log('characterReducer: ', action.payload);
      return removeItemFromStore(state, action.payload);
    default:
      return state;
  }
};
export default characterReducer;
