import {
  ADD_COMICS_TO_FAVORITE,
  REMOVE_COMICS_FROM_FAVORITE,
  ADD_CHARACTER_TO_FAVORITE,
  REMOVE_CHARACTER_FROM_FAVORITE, FETCH_CHARACTERS_START, FETCH_COMICS_START,
} from '../constants/actionTypes';
import { readFavorites } from '../../utils/serverStorage';

export const setComicsToFavorite = item => ({
  type: ADD_COMICS_TO_FAVORITE,
  payload: item,
});
export const removeComicsFromFavorite = id => ({
  type: REMOVE_COMICS_FROM_FAVORITE,
  payload: id,
});

export const setCharacterToFavorite = item => ({
  type: ADD_CHARACTER_TO_FAVORITE,
  payload: item,
});
export const removeCharacterFromFavorite = id => ({
  type: REMOVE_CHARACTER_FROM_FAVORITE,
  payload: id,
});

export const fetchCharactersSuccess = characters => ({
  type: FETCH_CHARACTERS_START,
  payload: characters,
  isLoading: false,
});
export const fetchComicsSuccess = comics => ({
  type: FETCH_COMICS_START,
  payload: comics,
});

export const fetchFavorites = (...path) => {
  const [characters, comics] = path;

  return async dispatch => {
    const charactersData = await readFavorites(characters);
    const comicsData = await readFavorites(comics);

    if (charactersData && charactersData !== {}) {
      // console.log('CHARACTER D A T A', charactersData);
      dispatch(fetchCharactersSuccess(charactersData));
    }

    if (comicsData && comicsData !== {}) {
      // console.log('COMICS D A T A', comicsData);
      dispatch(fetchComicsSuccess(comicsData));
    }
  };
};
