import axios from '../axios/axiosMarvel';

export const getApiMarvel = async url => {
  try {
    const response = await axios.get(url);
    if (response.data.status !== 'Ok') {
      console.log('Cannot receive data (response status no OK): ', response.data.status);
      return false;
    }
    return response.data.data.results;
  } catch (e) {
    console.log('Cannot receive data: ', e.message);
    return false;
  }
};
