import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { API_COMICS, QUERY_START_YEAR } from '../../Constants/Api';
import { getApiMarvel } from '../../utils/getApiMarvel';
import withErrorApi from '../../hoc/withErrorApi/withErrorApi';
import Loader from '../../Components/Loader/Loader';
import ComicsList from '../../Components/Comics/ComicsList/ComicsList';
import classes from './Comics.module.css';

const COMICS_START_WITH = `${API_COMICS}?${QUERY_START_YEAR}`;

const Comics = ({ setErrorApi }) => {
  const [comics, setComics] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [comicsYear, setComicsYear] = useState(null);
  const [sliderCustomYear, setSliderCustomYear] = useState(false);
  const cleanupFunction = useRef(false);

  const getComics = async url => {
    const res = await getApiMarvel(url);
    if (res) {
      const comicsList = res.map(({ id, title, thumbnail: { path, extension } }) => ({
        id, title, path, extension,
      }));

      if (!cleanupFunction.current) {
        setComics(comicsList);
        setIsLoading(false);
        setErrorApi(false);
      }
    } else {
      setErrorApi(true);
    }
  };

  useEffect(() => {
    cleanupFunction.current = false;
    if (comicsYear) {
      const comicsWithYear = `${COMICS_START_WITH}=${comicsYear}`;
      setSliderCustomYear(true);
      getComics(comicsWithYear);
    } else {
      getComics(API_COMICS);
    }

    // console.log('Comics rendered');
    return () => {
      cleanupFunction.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [comicsYear]);

  const setYearHandler = year => {
    setComicsYear(year);
  };

  return (
    <section className={classes.comics__wrapper}>
      { isLoading
        ? <Loader />
        : (
          <ComicsList
            comics={comics}
            setYearHandler={setYearHandler}
            customYear={sliderCustomYear}
            setCustomYear={setSliderCustomYear}
          />
        )}
    </section>
  );
};

Comics.propTypes = {
  setErrorApi: PropTypes.func,
};

export default withErrorApi(Comics);
