import React from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { STANDART_FANTASTIC } from '../../Constants/Api';
import { setCharacterToFavorite, removeCharacterFromFavorite } from '../../store/actions/favoriteActions';
import iconFavoriteEmpty from './img/favoriteEmpty.svg';
import iconFavoriteFill from './img/favoriteFill.svg';
import classes from './CharactersList.module.css';

const CharactersList = ({ character }) => {
  const characterState = useSelector(state => state.characterReducer);
  const dispatch = useDispatch();

  const dispatchCharacterFavorite = (...comicsItem) => {
    const [id, name, imgSrc] = comicsItem;

    if (characterState[id]) {
      dispatch(removeCharacterFromFavorite([id]));
    } else {
      const item = {
        [id]: {
          title: name,
          img: imgSrc,
        },
      };
      dispatch(setCharacterToFavorite(item));
    }
  };

  return (
    <ul className={classes.character__list}>
      {
              character.length
                ? character.map(({
                  id, name, path, extension,
                }, idx) => {
                  if (path.lastIndexOf('image_not_available') === -1) {
                    const imgSrc = `${path}/${STANDART_FANTASTIC}.${extension}`;
                    return (
                      <li key={name + idx} className={classes.character__item}>
                        <h3 className={classes.character__name}>{name}</h3>
                        <img src={imgSrc} alt="" className={classes.character__img} />
                        <img
                          src={characterState[id] ? iconFavoriteFill : iconFavoriteEmpty}
                          alt="icon-favorite"
                          className={classes.character__favorite}
                          onClick={() => {
                            dispatchCharacterFavorite(id, name, imgSrc);
                          }}
                        />
                      </li>
                    );
                  }
                  return null;
                })
                : <h3 className={classes.no__results}>NO RESULTS...</h3>
          }
    </ul>
  );
};

CharactersList.propTypes = {
  character: PropTypes.array,
};

export default CharactersList;
