import React from 'react';
import PropTypes from 'prop-types';
import classes from './Button.module.css';

const cls = [
  classes.button,
];

const Button = ({
  onClick, disabled, nameBtn, long, nameAtr,
}) => {
  if (long && !cls.includes(classes.long)) {
    cls.push(classes.long);
  }

  return (
    <button
      className={long ? cls.join(' ') : classes.button}
      onClick={onClick}
      disabled={disabled}
      type="button"
      name={nameAtr || null}
    >
      {nameBtn}
    </button>
  );
};

Button.propTypes = {
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  nameBtn: PropTypes.string,
  nameAtr: PropTypes.string,
  long: PropTypes.bool,
};

export default Button;
