import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import FavoriteItem from '../../Components/FavoriteItem/FavoriteItem';
import { removeComicsFromFavorite, removeCharacterFromFavorite } from '../../store/actions/favoriteActions';
import classes from './Favorites.module.css';

const Favorites = () => {
  const [comics, setComics] = useState([]);
  const [characters, setCharacters] = useState([]);
  const removeComicsId = useRef([]);
  const removeCharacterId = useRef([]);
  const stateComics = useSelector(state => state.comicsReducer);
  const stateCharacters = useSelector(state => state.characterReducer);

  const dispatch = useDispatch();

  useEffect(() => {
    const arrComics = Object.entries(stateComics);
    const arrCharacters = Object.entries(stateCharacters);

    const resComics = arrComics.map(item => ({
      id: item[0],
      ...item[1],
    }));
    const resCharacters = arrCharacters.map(item => ({
      id: item[0],
      ...item[1],
    }));

    setComics(resComics);
    setCharacters(resCharacters);

    // console.log('Favorites rendered');

    return () => {
      if (removeComicsId.current.length) {
        dispatch(removeComicsFromFavorite(removeComicsId.current));
      }
      if (removeCharacterId.current.length) {
        dispatch(removeCharacterFromFavorite(removeCharacterId.current));
      }
    };
  }, [stateComics, stateCharacters, dispatch]);

  const removeComicsHandler = comicsId => {
    removeComicsId.current = comicsId;
    // console.log('removeItemId.current', removeComicsId.current);
  };
  const removeCharacterHandler = characterId => {
    removeCharacterId.current = characterId;
    // console.log('removeItemId.current', removeCharacterId.current);
  };

  return (
    <div className={classes.favorites__wrapper}>
      <h2>Favorite Characters</h2>
      <hr />
      {characters.length
        ? <FavoriteItem items={characters} removeItem={removeCharacterHandler} />
        : <h3 className={classes.no__results}>No favorites characters...</h3>}

      <h2>Favorite Comics</h2>
      <hr />
      {comics.length
        ? <FavoriteItem items={comics} removeItem={removeComicsHandler} />
        : <h3 className={classes.no__results}>No favorites comics...</h3>}
    </div>
  );
};

export default Favorites;
