import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import ComicsItem from '../ComicsItem/ComicsItem';
import Select from '../../UI/Select/Select';
import Button from '../../UI/Button/Button';
import classes from './ComicsList.module.css';

const yearList = [2021, 2020, 2019, 2018, 2017];

const ComicsList = ({
  comics, setYearHandler, customYear, setCustomYear,
}) => {
  const sliderTrack = useRef(null);
  const counterWidth = useRef(0);
  const [buttonBlockPrev, setButtonBlockPrev] = useState(true);
  const [buttonBlockNext, setButtonBlockNext] = useState(false);
  const [comicsYear, setComicsYear] = useState(`${yearList[0]}`);
  const [imgWidth, setImgWidth] = useState(null);

  const nextSlideHandler = () => {
    const trackWidth = Number.parseInt(getComputedStyle(sliderTrack.current).width, 10);
    // if (trackWidth - counterWidth.current === 1200) {
    //   setButtonBlockNext(true);
    // }
    if (trackWidth - counterWidth.current === imgWidth * 2) {
      setButtonBlockNext(true);
    }

    // counterWidth.current += 600;
    counterWidth.current += imgWidth;
    setButtonBlockPrev(false);
    sliderTrack.current.style.transform = `translateX(${-counterWidth.current}px)`;
  };
  const prevSlideHandler = () => {
    if (counterWidth.current > 0) {
      counterWidth.current -= imgWidth;
      setButtonBlockNext(false);
      sliderTrack.current.style.transform = `translateX(${-counterWidth.current}px)`;
    }
    if (counterWidth.current === 0) {
      setButtonBlockPrev(true);
    }
  };

  const formHandler = event => {
    event.preventDefault();
    setYearHandler(comicsYear);
    setCustomYear(false);
  };

  useEffect(() => {
    if (customYear) {
      counterWidth.current = 0;
      sliderTrack.current.style.transform = `translateX(${counterWidth.current}px)`;
      setButtonBlockPrev(true);
    }
    // console.log('Comics List Rendered');
  }, [customYear]);

  return (
    <>
      <Select
        formHandler={formHandler}
        comicsYear={comicsYear}
        setComicsYear={setComicsYear}
        yearList={yearList}
      />
      <div className={classes.comics__container}>
        <div className={classes.comics__track} ref={sliderTrack}>
          {
                    comics.length
                      ? <ComicsItem comics={comics} setImgWidth={setImgWidth} />
                      : <h3 className={classes.no__results}>NO RESULTS...</h3>
                }
        </div>
      </div>
      <Button nameBtn="prev" long onClick={() => prevSlideHandler()} disabled={buttonBlockPrev} />
      <Button nameBtn="next" long onClick={() => nextSlideHandler()} disabled={buttonBlockNext} />
    </>
  );
};
ComicsList.propTypes = {
  comics: PropTypes.array,
  setYearHandler: PropTypes.func,
  customYear: PropTypes.bool,
  setCustomYear: PropTypes.func,
};

export default ComicsList;
