import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
import rootReducer from './reducers/rootReducer';
// import { writeCharacters, writeComics } from '../utils/serverStorage';
import { setLocalStorage } from '../utils/localStorage';

const store = createStore(
  rootReducer,
  composeWithDevTools(applyMiddleware(thunk)),
);

store.subscribe(() => {
  // writeCharacters(store.getState().characterReducer);
  // writeComics(store.getState().comicsReducer);
  setLocalStorage('characters', store.getState().characterReducer);
  setLocalStorage('comics', store.getState().comicsReducer);
});

export default store;
